	module.exports = {"data":{"markdownRemark":{"html":"<h2>Hover</h2>\n<p>  The <code>selector:hover</code> pseudo-class is triggered whene you interact with the element (selector) with a pointing device generally a mouse pointer. The styles of the element hovered over will be overridden by style defined in <code>selector:hover</code> pseudo-class.<br>\nTo style links/elements properly the rules should be defined in the order :- <br>\n:link - :visited - :hover - :active</p>\n<p><strong>Syntax :</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code> selector:hover {\n    css declarations;\n }</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/CSS/%3Ahover' target='_blank'>MDN Web Docs</a><br>\n<a href='https://www.w3schools.com/cssref/sel_hover.asp' target='_blank'>w3schools</a></p>","fields":{"slug":"/css/selectors/pseudo/hover/"},"frontmatter":{"title":"Hover"}}},"pathContext":{"slug":"/css/selectors/pseudo/hover/"}}

